<template lang="">
    <button
        :class="'btn btn-lg btn-block btn-primary '+ w100"
        :disabled="isLoading(action)"
        type="submit"
        value="Login"
    >
        <span class="d-flex justify-content-center align-items-center">
        {{name}}
        <LoadingOutlined v-if="isLoading(action)" class="ms-3 fs-5"/>
        </span>
    </button>
</template>
<script>
import {LoadingOutlined} from '@ant-design/icons-vue';

export default {
    props:['name','action', 'w100'],
    components: {
        LoadingOutlined
    },
    methods:{
        isLoading(actionName) {
            return this.$store.state.Loading[actionName] || false;
        },
    }
}
</script>
