    AddTax({commit , state}, Tax){
      axios.post(api + "create-taxes", Tax, {
        headers:{
          Authorization: 'Bearer ' + VueCookies.get('UserToken')
        }
      }) 
      .then((response) => {
        console.log(response)
        if(response.data.message){
          commit("SET_MESSAGE_POST", response.data.message)
          commit("SET_MESSAGE_COLOR", " alert-danger")
          setTimeout(() => {
            commit("SET_MESSAGE_POST", "")
            commit("SET_MESSAGE_COLOR", "")
          }, 2500);
        }
        if(response.data.Status_code === "200"){
          commit("ADD_TAX", Tax)
          state.tax.push({
            name: Tax.tax_name,
            rate: Tax.tax_value,
          })
          commit("SET_MESSAGE_POST", response.data.Message)
          commit("SET_MESSAGE_COLOR", " alert-success")
        }
      })
      .catch((error) => {
        checkUserLogin(error.response.data.message)
        if(error){
          commit("SET_MESSAGE_POST", "Something went wrong")
          commit("SET_MESSAGE_COLOR", " alert-danger")
        }
      })
    },
