    DeleteAPI({commit, dispatch},{id, name, action}){
      console.log(id)
      console.log(name)
      console.log(action)
      new swal({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        type: "warning",
        showCancelButton: !0,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
        confirmButtonClass: "btn btn-primary",
        cancelButtonClass: "btn btn-danger ml-1",
        buttonsStyling: !1,
      }).then(function (t) {
        if(t.isConfirmed){
          axios.post(api + name+"-delete",{ id: id.toString() },{
            headers:{
              Authorization: 'Bearer ' + VueCookies.get('UserToken')
            },
          })
          .then(response=>{
            if(response.data.Status_code === "202"){
              dispatch('Get'+action)
              Swal.fire({
                type: "success",
                title: "Deleted!",
                text: "Your file has been deleted.",
                confirmButtonClass: "btn btn-success",
              });
            }
          })
          .catch(error=>{
            checkUserLogin(error.response.data.message)
            notification['info']({
              message: `Error`,
              description: "Something went wrong",
            });
          })
        }

      });

    },
