<template>
    <!-- Main Wrapper -->
    <div class="main-wrapper">
      <layout-header></layout-header>
      <layout-sidebar></layout-sidebar>
      <!-- Page Wrapper -->
      <div class="page-wrapper">
        <div class="content container-fluid">
          <page-header name="Stocks" type="modal" modal="add_stock"/>
          <card-products></card-products>
  
          <!-- Table -->
          <div class="row">
            <div class="col-sm-12">
              <div class="card-table">
                <div class="card-body">
                  <div class="table-responsive">
                    <a-table
                      class="stripped table-hover"
                      :columns="columns"
                      :data-source="stocks"
                      @change="onChange"
                    >
                      <template #bodyCell="{ column, record }">
                        <template v-if="column.key === 'location'">
                          {{ record.location ? record.location['name'] : '-'  }}
                        </template>
                        <template v-if="column.key === 'product'">
                          {{ record.product ? record.product['name'] : '-'  }}
                        </template>
                        <template v-else-if="column.key === 'Action'">
                          <div class="d-flex align-items-center">
                            <router-link class="dropdown-item btn-action-icon me-2" :to="'/customer-details/'+record.id"
                                    ><i class="far fa-eye me-2"></i></router-link
                              >
                              <a
                                class="dropdown-item btn-action-icon"
                                href="javascript:void(0);"
                                @click="$store.dispatch('DeleteAPI', {id: record.id, name: 'customer', action:'Customers'})"
                                ><i class="far fa-trash-alt me-2 text-danger"></i></a
                              >
                          </div>
                        </template>
                      </template>
                    </a-table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- /Table -->
        </div>
      </div>
      <!-- /Page Wrapper -->
    </div>
      <!-- Add Category Modal -->
    <div class="modal custom-modal fade" id="add_stock" role="dialog">
      <div class="modal-dialog modal-dialog-centered modal-lg">
        <div class="modal-content">
          <div class="modal-header border-0 pb-0">
            <div class="form-header modal-header-title text-start mb-0">
              <h4 class="mb-0">Add Stock</h4>
            </div>
            <button type="button" class="close" data-bs-dismiss="modal" aria-label="Close">
              <span class="align-center" aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12">
                <div class="card-body">
                  <Form 
                    class="form-group-item border-0 pb-0 mb-0"
                    @submit="onSubmitStock"
                    :validation-schema="StockSchema"
                    v-slot="{ errors }"
                  >
                    <div class="row">
                      <div class="col-lg-4 col-sm-12">
                        <div class="form-group">
                          <label>Name <span class="text-danger">*</span></label>
                          <Field
                            type="text"
                            class="form-control"
                            name="product_name"
                            placeholder="Enter Title"
                            :class="{ 'is-invalid': errors.product_name }"
                          />
                          <div class="invalid-feedback">{{ errors.product_name }}</div>
                        </div>
                      </div>
                      <div class="col-lg-4 col-sm-12">
                        <div class="form-group">
                          <label>Product SKU <span class="text-danger">*</span></label>
                          <Field
                            type="number"
                            class="form-control"
                            name="product_sku"
                            placeholder="0"
                            :class="{ 'is-invalid': errors.product_sku }"
                          />
                          <div class="invalid-feedback">{{ errors.product_sku }}</div>
                        </div>
                      </div>
                      <div class="col-lg-4 col-sm-12">
                        <div class="form-group">
                          <label>Product Quantity <span class="text-danger">*</span></label>
                          <Field
                            type="number"
                            class="form-control"
                            name="product_quantity"
                            placeholder="0"
                            :class="{ 'is-invalid': errors.product_quantity }"
                          />
                          <div class="invalid-feedback">{{ errors.product_quantity }}</div>
                        </div>
                      </div>
                      <div class="col-lg-12 col-sm-12">
                        <div class="form-group">
                          <label>Select Location</label>
                          <Field 
                            as="select"
                            name="location" 
                            class="form-control"
                            id="location"
                            :class="{ 'is-invalid': errors.location }"
                          >
                            <option value="" disabled selected>Location</option>
                            <option value="1">UAE</option>
                          </Field>
                        </div>
                      </div>
                      {{ category_type }}
                      <div class="col-lg-12 col-sm-12">
                        <div class="form-group">
                          <label>Product</label>
                          <Field 
                            as="select"
                            name="item" 
                            class="form-control"
                            id="item"
                            :class="{ 'is-invalid': errors.item }"
                          >
                            <option value="" disabled selected>Select Product</option>
                            <option :value="product.id" v-for="product in products" :key="product.id">{{ product.name }}</option>
                          </Field>
                          <div class="invalid-feedback">{{ errors.item }}</div>
                        </div>
                      </div>
                      <div class="col-lg-12 col-sm-12">
                        <div class="form-group">
                          <label>Note</label>
                          <Field 
                            as="textarea"
                            name="description" 
                            class="form-control"
                            id="descriptionn"
                            :class="{ 'is-invalid': errors.descriptionn }"
                          >
                          </Field>
                          <div class="invalid-feedback">{{ errors.descriptionn }}</div>
                        </div>
                      </div>
                    </div>
                    <div class="modal-footer">
                      <a
                        href="javascript:;"
                        data-bs-dismiss="modal"
                        class="btn btn-primary paid-cancel-btn me-2 dismiss-modal"
                        >Cancel</a
                      >
                      <loading-button name="Save Changes" action="AddStock"/>
                    </div>
                  </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  <!-- /Add Category Modal -->
    <transition name="fade" mode="out-in">
      <loading name="GetStocks"></loading>
    </transition>
    <!-- /Main Wrapper -->
  </template>
  <script>
  const columns = [
    {
      title: "#",
      dataIndex: "id",
      sorter: {
        compare: (a, b) => {
          a = a.id.toLowerCase();
          b = b.id.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "User",
      dataIndex: "user",
      key: "id",
      sorter: (a, b) => a.user.localeCompare(b.user),
    },
    {
      title: "Warehouse",
      dataIndex: "location",
      key:"location",
      sorter: {
        compare: (a, b) => {
          a = a.location.toLowerCase();
          b = b.location.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Products",
      dataIndex: "product",
      key: "product",
      sorter: {
        compare: (a, b) => {
          a = a.product.toLowerCase();
          b = b.product.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Notes",
      dataIndex: "note",
      key: "note",
      sorter: {
        compare: (a, b) => {
          a = a.note.toLowerCase();
          b = b.note.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Action",
      key: "Action",
      sorter: true,
    },
  ];
  
  import { mapActions, mapState } from "vuex";
  import * as Yup from "yup";
  import { Form, Field } from "vee-validate";

  export default {
    data() {
      return {
        title: "Stocks",
        text: "Create Stock",
        title1: "Delete Category",
        columns,
      };
    },
    setup() {
      const StockSchema = Yup.object().shape({
        location: Yup.string().required("Location is required"),
        item: Yup.string().required("Product is required"),
        product_name: Yup.string().required("Product is required"),
        product_sku: Yup.string().required("SKU is required"),
        product_quantity: Yup.string().required("Prodcut Quantity is required"),
        description: Yup.string().required("Description is required"),
      });
      return {
        StockSchema
      };
    },
    computed: {
      ...mapState(['products', 'stocks']),
    },
    methods: {
      ...mapActions(['GetStocks','GetProducts']),
      async fetchStocks() {
        await this.GetStocks();
      },
      async GetProductsData(){
        await this.GetProducts()
      },
      onSubmitStock(stock){
        this.$store.dispatch("AddStock", stock)
      },
    },
    components: {
        Form,
        Field,
    },
    mounted() {
      this.fetchStocks();
      this.GetProductsData();
    },
  };
  </script>
  