<template>
  <!-- Main Wrapper -->
  <div class="main-wrapper">
    <layout-header></layout-header>
    <layout-sidebar></layout-sidebar>
    <!-- Page Wrapper -->
    <div class="page-wrapper">
      <div class="content container-fluid">
        <componentheader :title="title" />
        <div class="row">
          <div class="col-md-12">
            <div class="card">
              <Form 
              class="card-body"
              @submit="onSubmit"
              :validation-schema="schema"
              v-slot="{ errors }"
              >
                <div class="form-group-item border-0 mb-0">
                  <div class="row align-item-center">
                    <div class="col-lg-6 col-md-6 col-sm-12">
                      <div class="form-group">
                        <!-- {{ customers }} -->
                        <label>Customer Name</label>
                        <ul class="form-group-plus css-equal-heights">
                          <li>
                            <Field 
                              as="select" 
                              name="customer_id" 
                              class="form-control" 
                              id="customers"
                              :class="{ 'is-invalid': errors.customer_id }"
                            >
                              <option value="" disabled selected>Select Customer</option>
                              <option :value="customer.id" v-for="customer in customers" :key="customer.id">{{ customer.name }}</option>
                            </Field>
                            <div class="invalid-feedback">{{ errors.customer_id }}</div>
                            <!-- <vue-select
                              :options="customers.name"
                              placeholder="Choose Customer"
                            /> -->
                          </li>
                          <li>
                            <router-link
                              class="btn btn-primary form-plus-btn"
                              to="add-customer"
                            >
                              <i class="feather feather-plus-circle"></i>
                            </router-link>
                          </li>
                        </ul>
                      </div>
                    </div>
                    <div class="col-lg-6 col-md-6 col-sm-12">
                      <div class="form-group">
                        <label>Issue Date</label>
                        <div class="cal-icon">
                          <datepicker
                            v-model="IssueDate"
                            class="picker"
                            :editable="true"
                            :clearable="false"
                          />
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-6 col-md-6 col-sm-12">
                      <div class="form-group">
                        <label>Due Date</label>
                        <div class="cal-icon">
                          <datepicker
                            v-model="DueDate"
                            class="picker"
                            :editable="true"
                            :clearable="false"
                          />
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-6 col-md-6 col-sm-12">
                      <div class="form-group">
                        <label>Category</label>
                        <ul class="form-group-plus css-equal-heights">
                          <li>
                            <Field 
                              as="select"
                              name="category_id" 
                              class="form-control"
                              id="category_id"
                              :class="{ 'is-invalid': errors.category_id }"
                            >
                              <option value="" disabled selected>Select Category</option>
                              <option :value="category.id" v-for="category in categories" :key="category.id">{{ category.name }}</option>
                            </Field>
                            <div class="invalid-feedback">{{ errors.category_id }}</div>
                          </li>
                          <li>
                            <a class="btn btn-primary form-plus-btn" href="javascript:void(0);" data-bs-toggle="modal" data-bs-target="#add_category"><i class="feather feather-plus-circle" aria-hidden="true"></i></a>
                          </li>
                        </ul>
                      </div>
                    </div>
                    <div class="col-lg-12">
                      <div class="form-group">
                        <label>Products</label>
                        <ul class="form-group-plus css-equal-heights">
                          <li>
                            <Field
                              as="select"
                              @change="getCurrentProduct($event)" 
                              name="productsList"
                              class="form-control"
                              :class="{ 'is-invalid': errors.productsList }"
                            >
                              <option value="" selected>Select Prodcut</option>
                              <option v-for="product in products" :key="product.id" :value="product.id">{{ product.name }}</option>
                            </Field>
                            <div class="invalid-feedback">{{ errors.productsList }}</div>
                          </li>
                          <li>
                            <a class="btn btn-primary form-plus-btn" href="javascript:void(0);" data-bs-toggle="modal" data-bs-target="#add_product"><i class="feather feather-plus-circle" aria-hidden="true"></i></a>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="form-group-item">
                  <div class="card-table">
                    <div class="card-body">
                      <div class="table-responsive">
                        <a-table
                          class="stripped table-hover"
                          :columns="columns"
                          :data-source="data"
                          @change="onChange"
                        >
                          <template #bodyCell="{ column, record }">
                            <template v-if="column.key === 'action'">
                              <a
                                href="javascript:;"
                                class="btn-action-icon"
                                data-bs-toggle="modal"
                                @click="$store.state.clickedItem = record.id"
                                data-bs-target="#delete_discount"
                                :data-id='record.id'
                                ><span><i class="feather feather-trash-2 text-danger"></i></span
                              ></a>
                            </template>
                            <template v-if="column.key === 'purchase_price'">
                              <input 
                              type="text" class="form-control number-input" 
                              v-model="record.purchase_price"
                              @input="updateValue($event.target.value, 'purchase_price', record)"
                              placeholder="0">
                            </template>
                            <template v-if="column.key === 'amount'">
                              <input 
                                type="text" 
                                class="form-control number-input"
                                v-model="record.sale_price"
                                @input="updateValue($event.target.value, 'sale_price', record)"
                                placeholder="0"
                               >
                            </template>
                            <template v-if="column.key === 'tax'">
                              15%
                            </template>
                            <template v-if="column.key === 'quantity'">
                              <input 
                                type="text" 
                                class="form-control number-input" 
                                v-model="record.quantity" 
                                @input="updateValue($event.target.value, 'quantity', record)"
                                placeholder="0"
                              >
                            </template>
                            <template v-if="column.key === 'sale_price'">
                              {{ record.sale_price == null ? 0.00 : (record.sale_price * record.quantity).toFixed(2) }}
                              
                            </template>
                          </template>
                        </a-table>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="form-group-item border-0 p-0">
                  <div class="row">
                    <div class="col-lg-6">
                      <div class="form-group-bank">
                        <div class="invoice-total-box">
                          <div class="invoice-total-inner">
                            <p>Sub Total Amount Excluding VAT	 <span>{{ $store.getters.SubtotalAmountExcludingVAT }}</span></p>
                            <p>Total Discount	 <span>{{ $store.getters.totalDiscount == NaN ? 0.00 : $store.getters.totalDiscount }}</span></p>
                            <p>Total Amount Excluding VAT <span>{{$store.getters.totalAmountExcludingVAT}}</span></p>
                            <p>VAT KSA @ 0 % <span>{{ $store.getters.TotalVat }}</span></p>
                          </div>
                          <div class="invoice-total-footer">
                            <h4>Total Amount Including VAT <span>{{ $store.getters.TotalAmountIncludingVAT }}</span></h4>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="add-customer-btns text-end">
                  <button type="reset" class="btn btn-primary cancel me-2">Cancel</button>
                  <loading-button name="Save Changes" action="AddInvoice"/>
                </div>
              </Form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- /Page Wrapper -->
      <!-- Delete Items Modal -->
  <div class="modal custom-modal fade" id="delete_discount" role="dialog">
    <div class="modal-dialog modal-dialog-centered modal-md">
      <div class="modal-content">
        <div class="modal-header border-0 justify-content-center pb-0">
          <div class="form-header modal-header-title text-center mb-0">
            <h4 class="mb-2">Delete Product / Services</h4>
            <p>Are you sure want to delete?</p>
          </div>
        </div>
        <div class="modal-body">
          <div class="modal-btn delete-action">
            <div class="row">
              <div class="col-6">
                <a
                  href="javascript:;"
                  data-bs-dismiss="modal"
                  @click="$store.dispatch('ReduceProductServiceItem', $store.state.clickedItem)"
                  class="btn btn-primary paid-continue-btn"
                  >Delete</a
                >
              </div>
              <div class="col-6">
                <a
                  href="javascript:;"
                  data-bs-dismiss="modal"
                  class="btn btn-primary paid-cancel-btn"
                  >Cancel</a
                >
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- /Delete Items Modal -->
    <pagemodal :banktitle="banktitle" />
    <locationmodal :title1="title" />
  </div>
  <!-- /Main Wrapper -->
</template>
<script>
const columns = [
  {
    title: "Product / Service",
    dataIndex: "name",
    sorter: {
      compare: (a, b) => {
        a = a.name.toLowerCase();
        b = b.name.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Description",
    dataIndex: "description",
    sorter: {
      compare: (a, b) => {
        a = a.description.toLowerCase();
        b = b.description.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Quantity",
    dataIndex: "quantity",
    key: "quantity",
    sorter: (a, b) => a.quantity.localeCompare(b.quantity),
  },
  {
    title: "Discount",
    dataIndex: "purchase_price",
    key: "purchase_price",
    sorter: (a, b) => a.purchase_price.localeCompare(b.purchase_price),
  },
  {
    title: "Amount",
    dataIndex: "amount",
    key: 'amount',
    sorter: (a, b) => a.amount.localeCompare(b.amount),
  },
  {
    title: "Tax (%)",
    dataIndex: "tax_id",
    key:"tax",
    sorter: {
      compare: (a, b) => {
        a = a.tax_id.toLowerCase();
        b = b.tax_id.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Amount",
    dataIndex: "sale_price",
    key: 'sale_price',
    sorter: (a, b) => a.sale_price.localeCompare(b.sale_price),
  },
  {
    title: "Action",
    key: "action",
    sorter: true,
  },
];
const data = [];
import { ref } from "vue";
import * as Yup from "yup";
import { Form, Field } from "vee-validate";
import { mapState, mapActions } from 'vuex';
const issueDate = ref(new Date());
const dueDate = ref(new Date());
export default {
  components: {
    Form,
    Field,
  },
  data() {
    return {
      price: null,
      salePrice: null,
      activeField: '',
      IssueDate: issueDate,
      DueDate: dueDate,
      currentProduct:null,
      banktitle: "Add Bank Details",
      title: "Add Invoice",
      Customer: ["Choose Customer", "Customer 1", "Customer 2", "Customer 3"],
      GST: ["Without GST", "With GST"],
      Percentage: ["Percentage(%)", "0%", "5%", "10%", "15%"],
      Currency: ["Select Currency", "US dollar", "Euro", "Pound sterling", "Swiss franc"],
      data,
      columns,
    };
  },
  setup() {
    const schema = Yup.object().shape({
      customer_id: Yup.string().required("Customer is required"),
      category_id: Yup.string().required("Category is required"),
      productsList: Yup.string().required("Products is required"),
    });
    return {
      schema,
    };
  },
  computed:{
    ...mapState(['products', 'records', 'customers','added_customer', 'categories', 'tax']),
  },
  methods:{
    ...mapActions(['GetProducts', 'GetCustomers','GetCategories', 'GetTax']),
    async GetProductsData(){
      await this.GetProducts()
    },
    isLoading(actionName) {
        return this.$store.state.Loading[actionName] || false;
    },
    async GetTaxData(){
      await this.GetTax()
    },
    async fetchCustomers() {
      await this.GetCustomers();
    },
    async fetchCategories() {
      await this.GetCategories();
    },
    getCurrentProduct(event) {
    const productId = parseInt(event.target.value);
    const productIndex = this.products.findIndex(product => product.id === productId);
    
    if (productIndex !== -1) {
      const [selectedProduct] = this.products.splice(productIndex, 1);
      this.data.push(selectedProduct);
    } else {
      console.error('Product not found');
    }
  },
    checkValue(value) {
      return value.trim() === '' ? 0 : value;
    },
    updateValue(newValue, key, record) {
      record[key] = this.checkValue(newValue);
    },
    onSubmit(Invoice){
      var added_Invoice_data = []
      this.data.forEach(product =>{
        console.log(product)
        var obj = {
            "item": product.id,
            "description": product.description,
            "quantity": product.quantity,
            "price": product.sale_price,
            "discount": product.purchase_price,
            "tax": 15,
            "itemTaxRate": "0.00",
            "itemTaxPrice": "0.00"
        }

        added_Invoice_data.push(obj)
      })

      Invoice.items = added_Invoice_data
      Invoice.user_id = this.$store.state.user_id
      Invoice.issue_date = `${this.IssueDate.getFullYear()}-${this.IssueDate.getMonth() + 1}-${this.IssueDate.getDate()}`
      Invoice.due_date = `${this.DueDate.getFullYear()}-${this.DueDate.getMonth() + 1}-${this.DueDate.getDay()}`
      this.$store.dispatch("AddInvoice", Invoice)
    },
  },
  mounted() {
    this.GetProductsData();
    this.GetTaxData();
    this.fetchCustomers();
    this.fetchCategories();
    this.$store.state.records = this.data
  },
};
</script>
