<template>
  <div class="main-wrapper">
    <layout-header></layout-header>
    <layout-sidebar></layout-sidebar>

    <!-- Page Wrapper -->
    <div class="page-wrapper">
      <div class="content container-fluid">
        <page-header name="Customer" type="page"/>
        <div class="row">
          <div class="col-sm-12">
            <div class="card-table">
              <div class="card-body">
                <div class="table-responsive">
                  <a-table
                    class="stripped table-hover"
                    :columns="columns"
                    :data-source="customers"
                    @change="onChange"
                  >
                    <template #bodyCell="{ column, record }">
                      <template v-if="column.key === 'Name'">
                        <h2 class="table-avatar">
                          <router-link to="/profile"
                            >{{ record.Name }}<span>{{ record.Email }}</span></router-link
                          >
                        </h2>
                      </template>
                      <template v-else-if="column.key === 'Action'">
                        <div class="d-flex align-items-center">
                            <router-link class="dropdown-item btn-action-icon me-2" :to="'/customer-details/'+record.id"
                                    ><i class="far fa-eye me-2"></i></router-link
                              >
                              <a
                                class="dropdown-item btn-action-icon"
                                href="javascript:void(0);"
                                @click="$store.dispatch('DeleteAPI', {id: record.id, name: 'customer', action:'Customers'})"
                                ><i class="far fa-trash-alt me-2 text-danger"></i></a
                              >
                          </div>
                      </template>
                    </template>
                  </a-table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <transition name="fade" mode="out-in">
      <loading name="GetCustomers"></loading>
    </transition>
    <!-- /Page Wrapper -->
  </div>

  <!-- Add Vendor Modal -->
    <div class="modal custom-modal fade" id="edit_customer" role="dialog">
      <div class="modal-dialog modal-dialog-centered modal-lg">
        <Form 
          class="modal-content"
          @submit="onAddVendor"
          :validation-schema="schema"
        >
          <div class="modal-header border-0 pb-0">
            <div class="form-header modal-header-title text-start mb-0">
              <h4 class="mb-0">Edit Customer</h4>
            </div>
            <button type="button" class="close" data-bs-dismiss="modal" aria-label="Close">
              <span class="align-center" aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">

            </div>
          </div>
          <div class="modal-footer">
            <a
              href="javascript:;"
              data-bs-dismiss="modal"
              class="btn btn-primary paid-cancel-btn me-2 dismiss-modal"
              >Cancel</a
            >
          </div>
        </Form>
      </div>
    </div>
  <!-- /Add Vendor Modal -->
</template>

<script>
const columns = [
  {
    title: "#",
    dataIndex: "id",
    sorter: (a, b) => a.NUM.localeCompare(b.NUM),
  },
  {
    title: "Name",
    dataIndex: "name",
    key: "name",
    sorter: {
      compare: (a, b) => {
        a = a.name.toLowerCase();
        b = b.name.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Contact",
    dataIndex: "contact",
    sorter: {
      compare: (a, b) => {
        a = a.contact.toLowerCase();
        b = b.contact.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Email",
    dataIndex: "email",
    sorter: {
      compare: (a, b) => {
        a = a.email.toLowerCase();
        b = b.email.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Balance",
    dataIndex: "balance",
    sorter: {
      compare: (a, b) => {
        a = a.balance.toLowerCase();
        b = b.balance.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Last Login",
    dataIndex: "last_login_at",
    key: "last_login_at",
    sorter: {
      compare: (a, b) => {
        a = a.last_login_at.toLowerCase();
        b = b.last_login_at.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Action",
    key: "Action",
    sorter: true,
  },
];
import { ref } from "vue";
import { mapActions, mapState } from "vuex";
const currentDate = ref(new Date());
export default {
  data() {
    return {
      title1: "Delete FAQ",
      startdate: currentDate,
      columns,
      counter:0,
    };
  },
  computed: {
    ...mapState(['customers']),
  },
  methods: {
    ...mapActions(['GetCustomers']),
    async fetchCustomers() {
      await this.GetCustomers();
    },
  },
  mounted() {
    this.fetchCustomers();
  },
};
</script>
