<template>
    <!-- Main Wrapper -->
    <div class="main-wrapper">
      <layout-header></layout-header>
      <layout-sidebar></layout-sidebar>
  
      <!-- Page Wrapper -->
      <div class="page-wrapper">
        <div class="content container-fluid">
          <locationheader :title="title" :txt="txt" :text="text" />
  
          <!-- Table -->
          <div class="row">
            <div class="col-sm-12">
              <div class="card-table">
                <div class="card-body">
                  <div class="table-responsive">
                    <a-table
                      class="stripped table-hover"
                      :columns="columns"
                      :data-source="tax"
                      @change="onChange"
                    >
                      <template #bodyCell="{ column, record }">
                        <template v-if="column.key === 'No'">
                          <router-link to="invoice-details" class="invoice-link"
                            >EXP-148061</router-link
                          >
                        </template>
                        <template v-else-if="column.key === 'Name'">
                          <h2 class="table-avatar">
                            <p>Asset</p>
                          </h2>
                        </template>
                        <template v-else-if="column.key === 'Action'">
                          <div class="d-flex align-items-center">
                            <a
                              class="btn-action-icon me-2"
                              href="javascript:void(0);"
                              download
                              ><i class="feather feather-download"></i
                            ></a>
                            <div class="dropdown dropdown-action">
                              <a
                                href="javascript:;"
                                class="btn-action-icon"
                                data-bs-toggle="dropdown"
                                aria-expanded="false"
                                ><i class="fas fa-ellipsis-v"></i
                              ></a>
                              <div class="dropdown-menu dropdown-menu-right">
                                <ul>
                                  <li>
                                    <a
                                      class="dropdown-item"
                                      href="javascript:void(0);"
                                      data-bs-toggle="modal"
                                      data-bs-target="#edit_assets"
                                      ><i class="far fa-edit me-2"></i>Edit</a
                                    >
                                  </li>
                                  <li>
                                    <a
                                      class="dropdown-item"
                                      href="javascript:void(0);"
                                      data-bs-toggle="modal"
                                      data-bs-target="#delete_modal"
                                      ><i class="far fa-trash-alt me-2"></i>Delete</a
                                    >
                                  </li>
                                </ul>
                              </div>
                            </div>
                          </div>
                        </template>
                      </template>
                    </a-table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- /Table -->
        </div>
      </div>
      <!-- /Page Wrapper -->
      <locationmodal :title1="title1" />
    </div>
    <!-- /Main Wrapper -->
  </template>
  <script>
  import { mapActions, mapState } from "vuex";

  const columns = [
    {
      title: "Tax Name",
      dataIndex: "name",
      sorter: {
        compare: (a, b) => {
          a = a.Reference.toLowerCase();
          b = b.Reference.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Rate %",
      dataIndex: "rate",
      sorter: (a, b) => a.Amount.localeCompare(b.Amount),
    },
    {
      title: "Action",
      key: "Action",
      sorter: true,
    },
  ];
  const data = [
    {
      No: "1",
      Name: "Ahmed",
      PurchaseDate: "27/10/2023",
      SupporterDate: "27/10/2023",
      Amount: "$1,54,220",
      Description: "lorem",
    },
  ];
  export default {
    setup() {
      return {
        title: "Taxes",
        title1: "Add Tax",
        text: "Add Tax",
        txt: "#add_tax",
        data,
        columns,
      };
    },
    computed: {
    ...mapState(['tax']),
    },
    methods: {
      ...mapActions(['GetTax']),
      async fetchTaxes() {
        await this.GetTax();
      },
    },
    mounted() {
      this.fetchTaxes();
    },
  };
  </script>
  