import {createRouter, createWebHistory} from 'vue-router';
import Login from '@/views/pages/Auth/login'
import Register from '@/views/pages/Auth/register'
import Forgotpassword from '@/views/pages/Auth/forgotpassword'
import Lockscreen from '@/views/pages/Auth/lockscreen'
import Quotations from '@/views/pages/quotations/quotations'
import Stocks from '@/views/pages/stocks/stocks'
import Units from '@/views/pages/units/units'
import Products from '@/views/pages/products/products'
import DeliveryNotes from '@/views/pages/delivery-notes/delivery-notes'
import AddDeliveryNotes from '@/views/pages/delivery-notes/add-delivery-note'
import Categories from '@/views/pages/categories/categories'
import Brands from '@/views/pages/brands/brands.vue'
import Editquotations from '@/views/pages/quotations/edit-quotations'
import Addquotations from '@/views/pages/quotations/add-quotations'
import Addinvoice from '@/views/pages/invoices/add-invoice'
import Invoices from '@/views/pages/invoices/invoices'
import Suppliers from '@/views/pages/suppliers/suppliers'
import SupplierDetails from '@/views/pages/suppliers/supplier-details'
import Editinvoice from '@/views/pages/invoices/edit-invoice'
import Invoicedetails from '@/views/pages/invoices/invoice-details'
import Customers from '@/views/pages/customers/customers'
import AddCustomer from '@/views/pages/customers/add-customer'
import EditCustomer from '@/views/pages/customers/edit-customer'
import CustomerDetails from '@/views/pages/customers/customer-details'
import Index from '@/views/pages/dashboard/index'
import Assets from '@/views/pages/dashboard-settings/assets'
import Taxes from '@/views/pages/dashboard-settings/taxes'

const routes = [
  {
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/index',
    name: 'index',
    component: Index,
    meta: {
      headerClass: '',
      sidebarClass: '',
    }
  },
  {
    path: '/register',
    name: 'register',
    component: Register
  },
  {
    path: '/units',
    name: 'units',
    component: Units
  },
  {
    path: '/delivery-notes',
    name: 'delivery-notes',
    component: DeliveryNotes
  },
  {
    path: '/add-delivery-note',
    name: 'add-delivery-note',
    component: AddDeliveryNotes
  },
  {
    path: '/categories',
    name: 'categories',
    component: Categories
  },
  {
    path: '/products',
    name: 'products',
    component: Products
  },
  {
    path: '/brands',
    name: 'brands',
    component: Brands
  },
  {
    path: '/forgot-password',
    name: 'forgot-password',
    component: Forgotpassword
  },
  {
    path: '/lock-screen',
    name: 'lock-screen',
    component: Lockscreen
  },
  {
    path: '/quotations',
    name: 'quotations',
    component: Quotations
  },
  {
    path: '/stocks',
    name: 'stocks',
    component: Stocks
  },
  {
    path: '/vendors',
    name: 'vendors',
    component: Suppliers
  },
  {
    path: '/vendors-details/:id',
    name: 'vendors-details',
    component: SupplierDetails
  },
  {
    path: '/edit-quotations',
    name: 'edit-quotations',
    component: Editquotations
  },
  {
    path: '/add-quotations',
    name: 'add-quotations',
    component: Addquotations
  },
  {
    path: '/add-customer',
    name: 'add-customer',
    component: AddCustomer
  },
  {
    path: '/invoices',
    name: 'invoices',
    component: Invoices
  },
  {
    path: '/add-invoice',
    name: 'add-invoice',
    component: Addinvoice
  },
  {
    path: '/edit-invoice',
    name: 'edit-invoice',
    component: Editinvoice
  },
  {
    path: '/invoice-details/:id',
    name: 'invoice-details',
    component: Invoicedetails
  },
  {
    path: '/customers',
    name: 'customers',
    component: Customers
  },
  {
    path: '/edit-customer',
    name: 'edit-customer',
    component: EditCustomer
  },
  {
    path: '/customer-details/:id',
    name: 'customer-details',
    component: CustomerDetails
  },
  {
    path: '/assets',
    name: 'assets',
    component: Assets
  },
  {
    path: '/taxes',
    name: 'taxes',
    component: Taxes
  },
  ];
export const router = createRouter({
    history: createWebHistory('/'),
    linkActiveClass: 'active',
    routes,
});

