import ar from "@/localization/json/ar.json";
import en from "@/localization/json/en.json";
import { createI18n } from "vue-i18n";
var lang;
if(localStorage.getItem("lang")){
    lang = localStorage.getItem("lang")
}
console.log(lang)
const i18n = createI18n({
  locale: lang,
  legacy: false,
  messages:{
    ar,
    en
  }
})

export default i18n;