<template>
    <!-- Main Wrapper -->
    <div class="main-wrapper">
      <layout-header></layout-header>
      <layout-sidebar></layout-sidebar>
  
      <!-- Page Wrapper -->
      <div class="page-wrapper">
        <div class="content container-fluid">
          <page-header name="delivery-note" type="page" specific="Delivery Note"/>
          <!-- <filter /> -->
          <invoicecard />
          <!-- <invoicetab /> -->
          <!-- Table -->
          <div class="row">
            <div class="col-sm-12">
              <div class="card-table">
                <div class="card-body">
                  <div class="table-responsive">
                    <a-table :columns="columns" :data-source="delivery_notes" @change="onChange">
                      <template #bodyCell="{ column, record }">
                        <template v-if="column.key === '#'">
                          <label class="custom_check">
                            <input type="checkbox" name="invoice" />
                            <span class="checkmark"></span>
                          </label>
                          <router-link to="invoice-details" class="invoice-link">{{
                            record.InvoiceID
                          }}</router-link></template
                        >
                        <template v-else-if="column.key === 'dn_id'">
                          <b>#IDNO00{{ record.dn_id }}</b>
                        </template>
                        <template v-else-if="column.key === 'Status'">
                          <span class="rounded-pill badge bg-primary" v-if="record.status == 0">Draft</span>
                          <span class="rounded-pill badge bg-warning" v-if="record.status == 1">Sent</span>
                          <span class="rounded-pill badge bg-danger" v-if="record.status == 2">Cancel</span>
                          <span class="rounded-pill badge bg-info" v-if="record.status == 3">partially paid</span>
                          <span class="rounded-pill badge bg-success" v-if="record.status == 4">Paid</span>
                          </template
                        >
                        <template v-else-if="column.key === 'Action'">
                          <div class="d-flex align-items-center">
                            <router-link class="dropdown-item btn-action-icon me-2" :to="'/delivery-note-details/'+record.id"
                                    ><i class="far fa-eye me-2"></i></router-link
                              >
                              <a
                                class="dropdown-item btn-action-icon"
                                href="javascript:void(0);"
                                @click="$store.dispatch('DeleteAPI', {id: record.id, name: 'delivery-note', action:'DeliveryNotes'})"
                                ><i class="far fa-trash-alt me-2 text-danger"></i></a
                              >
                          </div>
                        </template>
                      </template>
                    </a-table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- /Table -->
        </div>
      </div>
      <!-- /Page Wrapper -->
      <transition name="fade" mode="out-in">
        <loading name="GetDeliveryNotes"></loading>
      </transition>
    </div>
    <!-- /Main Wrapper -->
  </template>
  <script>
  import { mapActions, mapState } from "vuex";
  
  export default {
    data() {
      return {
        title: this.$t("Invoices"),
        path: "add-invoice",
        text: this.$t("New Invoice"),
        title1: "Delete Invoice",
        response: [],
        columns: [],
      };
    },
    created(){
      this.columns = [
        {
          title: 'Delivery Notes',
          dataIndex: "dn_id",
          key: "dn_id",
          sorter: {
            compare: (a, b) => {
              a = a.dn_id.toLowerCase();
              b = b.dn_id.toLowerCase();
              return a > b ? -1 : b > a ? 1 : 0;
            },
          },
        },
        {
          title: "Date",
          dataIndex: "due_date",
          sorter: {
            compare: (a, b) => {
              a = a.due_date.toLowerCase();
              b = b.due_date.toLowerCase();
              return a > b ? -1 : b > a ? 1 : 0;
            },
          },
        },
        {
          title: "Issue Date",
          dataIndex: "issue_date",
          key: "issue_date",
          sorter: (a, b) => a.issue_date.localeCompare(b.issue_date),
        },
        {
          title: this.$t("Due Date"),
          dataIndex: "due_date",
          sorter: {
            compare: (a, b) => {
              a = a.due_date.toLowerCase();
              b = b.due_date.toLowerCase();
              return a > b ? -1 : b > a ? 1 : 0;
            },
          },
        },
        {
          title: this.$t("Status"),
          dataIndex: "Status",
          key: "Status",
          sorter: {
            compare: (a, b) => {
              a = a.Status.toLowerCase();
              b = b.Status.toLowerCase();
              return a > b ? -1 : b > a ? 1 : 0;
            },
          },
        },
        {
          title: this.$t("Action"),
          key: "Action",
          sorter: true,
        },
      ];
      this.response = [
        {
          InvoiceID: "#4987",
          email: "john@example.com",
          Balance: "$0",
          due_date: "-",
          issue_date: "-",
          Status: "Paid",
        },
      ];
    },
    computed: {
      ...mapState(['delivery_notes']),
    },
    methods: {
      ...mapActions(['GetDeliveryNotes']),
      async fetchDataDeliveryNotes() {
        await this.GetDeliveryNotes();
      },
    },
    mounted() {
      this.fetchDataDeliveryNotes();
    },
  };
  
  </script>
  