<template>
    <div class="main-wrapper">
      <layout-header></layout-header>
      <layout-sidebar></layout-sidebar>
  
      <!-- Page Wrapper -->
      <div class="page-wrapper">
        <div class="content container-fluid">
          <page-header name="Units" type="modal" modal="add_unit"/>
          <card-products></card-products>
          <!-- Table -->
          <div class="row">
            <div class="col-sm-12">
              <div class="card-table">
                <div class="card-body">
                  <div class="table-responsive">
                    <a-table
                      class="stripped table-hover"
                      :columns="columns"
                      :data-source="units"
                      @change="onChange"
                    >
                      <template #bodyCell="{ column, record }">
                        <template v-if="column.key === 'CategoryImage'">
                          <h2 class="table-avatar">
                            <img
                              class="avatar-img rounded"
                              width="30"
                              height="30"
                              :src="
                                require(`@/assets/img/category/${record.CategoryImage}`)
                              "
                              alt="User Image"
                            />
                          </h2>
                        </template>
                        <template v-else-if="column.key === 'Action'">
                          <div class="d-flex align-items-center">
                              <router-link class="dropdown-item btn-action-icon me-2" :to="'/invoice-details/'+record.id"
                                    ><i class="far fa-eye me-2"></i></router-link
                              >
                              <a
                                class="dropdown-item btn-action-icon"
                                href="javascript:void(0);"
                                @click="$store.dispatch('DeleteAPI', {id: record.id, name: 'unit', action:'Units'})"
                                ><i class="far fa-trash-alt me-2 text-danger"></i></a
                              >
                          </div>
                        </template>
                      </template>
                    </a-table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- /Table -->
        </div>
      </div>
      <!-- /Page Wrapper -->
      <transition name="fade" mode="out-in">
          <loading name="GetUnits"></loading>
      </transition>
      <!-- Add Category Modal -->
      <div class="modal custom-modal fade" id="add_unit" role="dialog">
        <div class="modal-dialog modal-dialog-centered modal-md">
          <div class="modal-content">
            <div class="modal-header border-0 pb-0">
              <div class="form-header modal-header-title text-start mb-0">
                <h4 class="mb-0">Add Unit</h4>
              </div>
              <button type="button" class="close" data-bs-dismiss="modal" aria-label="Close">
                <span class="align-center" aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-md-12">
                  <div class="card-body">
                    <Form 
                      class="form-group-item border-0 pb-0 mb-0"
                      @submit="onSubmitUnit"
                      :validation-schema="UnitSchema"
                      v-slot="{ errors }"
                    >
                      <div class="row">
                        <div class="col-lg-12 col-sm-12">
                          <div class="form-group">
                            <label>Unit Name <span class="text-danger">*</span></label>
                            <Field
                              type="text"
                              class="form-control"
                              name="name"
                              placeholder="Enter Title"
                              :class="{ 'is-invalid': errors.name }"
                            />
                            <div class="invalid-feedback">{{ errors.name }}</div>
                          </div>
                        </div>
                      </div>
                      <div class="modal-footer">
                        <a
                          href="javascript:;"
                          data-bs-dismiss="modal"
                          class="btn btn-primary paid-cancel-btn me-2 dismiss-modal"
                          >Cancel</a
                        >
                        <loading-button name="Add Unit" action="AddUnit"/>
                      </div>
                    </Form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- /Add Category Modal -->
    </div>
  </template>
  
  <script>
  const columns = [
    {
      title: "#",
      dataIndex: "id",
      sorter: (a, b) => a.id.localeCompare(b.id),
    },
    {
      title: "Unit Name",
      dataIndex: "name",
      sorter: {
        compare: (a, b) => {
          a = a.name.toLowerCase();
          b = b.name.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Action",
      key: "Action",
      sorter: true,
    },
  ];
  import { mapActions, mapState } from "vuex";
  import { ref } from "vue";
  import { Form, Field } from "vee-validate";
  import * as Yup from "yup";
  import VueCookies from 'vue-cookies'
  
  const currentDate = ref(new Date());
  export default {
    data() {
      return {
        title1: "Delete Category",
        startdate: currentDate,
        Parent_Category: ["None", "Coupons", "News", "Plugins", "Themes", "Tutorial"],
        Parent_Category1: ["None", "Coupons", "News", "Plugins", "Themes", "Tutorial"],
        columns,
      };
    },
    setup() {
      const UnitSchema = Yup.object().shape({
        name: Yup.string().required("Unit Name is required"),
      });
      return {
        UnitSchema,
      };
    },
    components: {
      Form,
      Field,
    },
    computed: {
      ...mapState(['units']),
    },
    methods: {
      ...mapActions(['GetUnits']),
      async fetchData() {
        await this.GetUnits();
      },
      onSubmitUnit(unit){
        this.$store.dispatch("AddUnit", unit)
      },
    },
    mounted() {
      this.fetchData();
    },
  };
  </script>
  