<template>
  <div class="main-wrapper">
    <layout-header></layout-header>
    <layout-sidebar></layout-sidebar>

    <!-- Page Wrapper -->
    <div class="page-wrapper">
      <div class="content container-fluid">
        <componentheader :title="title" />
        <div class="row">
          <div class="col-md-12">
            <Form 
              @submit="onSubmit"
              :validation-schema="schema"
              v-slot="{ errors }"
            >
              <div class="card-body">
                <div class="form-group-item">
                  <h5 class="form-title">Basic Details</h5>
                  <!-- <div class="profile-picture">
                    <div class="upload-profile">
                      <div class="profile-img">
                        <img
                          id="blah"
                          class="avatar"
                          src="@/assets/img/profiles/avatar-10.jpg"
                          alt=""
                        />
                      </div>
                      <div class="add-profile">
                        <h5>Upload a New Photo</h5>
                        <span>Profile-pic.jpg</span>
                      </div>
                    </div>
                    <div class="img-upload">
                      <label class="btn btn-primary">
                        Upload <input type="file" />
                      </label>
                      <a class="btn btn-remove">Remove</a>
                    </div>
                  </div> -->
                  <div class="row">
                    <div class="col-lg-4 col-md-6 col-sm-12">
                      <div class="form-group">
                        <label>Name</label>
                        <Field
                          type="text"
                          class="form-control"
                          name="customer_name"
                          placeholder="Enter Name"
                          :class="{ 'is-invalid': errors.customer_name }"
                        />
                        <div class="invalid-feedback">{{ errors.customer_name }}</div>
                      </div>
                    </div>
                    <div class="col-lg-4 col-md-6 col-sm-12">
                      <div class="form-group">
                        <label>Email</label>
                        <Field
                          type="email"
                          class="form-control"
                          name="email"
                          placeholder="Enter Email Address"
                          :class="{ 'is-invalid': errors.email }"
                        />
                        <div class="invalid-feedback">{{ errors.email }}</div>
                      </div>
                    </div>
                    <div class="col-lg-4 col-md-6 col-sm-12">
                      <div class="form-group">
                        <label>Phone</label>
                        <Field
                          type="tel"
                          class="form-control"
                          name="customer_contact"
                          placeholder="Enter phone"
                          :class="{ 'is-invalid': errors.customer_contact }"
                        />
                        <div class="invalid-feedback">{{ errors.customer_contact }}</div>

                        <!-- <div class="invalid-feedback">{{ errors.phone_customer }}</div> -->
                      </div>
                    </div>
                    <!-- <div class="col-lg-4 col-md-6 col-sm-12">
                      <div class="form-group">
                        <label>Currency</label>
                        <vue-select :options="Currency" placeholder="Select Currency" />
                      </div>
                    </div> -->
                    <div class="col-lg-4 col-md-6 col-sm-12">
                      <div class="form-group">
                        <label>VAT NO</label>
                        <Field
                          type="text"
                          class="form-control"
                          name="customer_vat_no"
                          placeholder="VAT Here"
                          :class="{ 'is-invalid': errors.customer_vat_no }"
                        />
                        <div class="invalid-feedback">{{ errors.customer_vat_no }}</div>
                      </div>
                    </div>
                    <div class="col-lg-4 col-md-6 col-sm-12">
                      <div class="form-group">
                        <label>CR NO</label>
                        <Field
                          type="text"
                          class="form-control"
                          name="customer_cr_no"
                          placeholder="CR Here"
                          :class="{ 'is-invalid': errors.customer_cr_no }"
                        />
                        <div class="invalid-feedback">{{ errors.customer_cr_no }}</div>
                      </div>
                    </div>
                    <div class="col-lg-4 col-md-6 col-sm-12">
                      <div class="form-group">
                        <label>Password</label>
                        <Field
                          type="password"
                          class="form-control"
                          name="customer_password"
                          placeholder="*************"
                          :class="{ 'is-invalid': errors.customer_password }"
                        />
                        <div class="invalid-feedback">{{ errors.customer_password }}</div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="form-group-item">
                  <div class="row">
                    <div class="col-md-6">
                      <div class="billing-btn mb-2">
                        <h5 class="form-title">Billing Address</h5>
                      </div>
                      <div class="form-group">
                        <label>Name</label>
                        <Field
                          type="text"
                          class="form-control"
                          name="customer_billing_name"
                          placeholder="Enter Name"
                          :class="{ 'is-invalid': errors.customer_billing_name }"
                        />
                        <div class="invalid-feedback">{{ errors.customer_billing_name }}</div>
                      </div>
                      <div class="form-group">
                        <label>Address</label>
                        <Field
                          type="text"
                          class="form-control"
                          name="customer_billing_address"
                          placeholder="Enter Address 1"
                          :class="{ 'is-invalid': errors.customer_billing_address }"
                        />
                        <div class="invalid-feedback">{{ errors.customer_billing_address }}</div>
                      </div>
                      <div class="row">
                        <div class="col-lg-6 col-md-12">
                          <div class="form-group">
                            <label>Country</label>
                            <Field
                              type="text"
                              class="form-control"
                              name="customer_billing_country"
                              placeholder="Country"
                              :class="{ 'is-invalid': errors.customer_billing_country }"
                            />
                            <div class="invalid-feedback">{{ errors.customer_billing_country }}</div>
                          </div>
                          <div class="form-group">
                            <label>City</label>
                            <Field
                              type="text"
                              name="customer_billing_city"
                              class="form-control"
                              placeholder="City"
                              :class="{ 'is-invalid': errors.customer_billing_city }"
                            />
                            <div class="invalid-feedback">{{ errors.customer_billing_city }}</div>
                          </div>
                          <div class="form-group">
                            <label>Phone</label>
                            <Field
                             type="tel"
                             class="form-control"
                             name="customer_billing_phone"
                             placeholder="Enter phone"
                            :class="{ 'is-invalid': errors.customer_billing_phone }"
                            />
                            <div class="invalid-feedback">{{ errors.customer_billing_phone }}</div>
                          </div>
                        </div>
                        <div class="col-lg-6 col-md-12">
                          <div class="form-group">
                            <label>State</label>
                            <Field
                              type="text"
                              class="form-control"
                              placeholder="State"
                              :class="{ 'is-invalid': errors.customer_billing_state }"
                              name="customer_billing_state"
                            />
                            <div class="invalid-feedback">{{ errors.customer_billing_state }}</div>
                          </div>
                          <div class="form-group">
                            <label>Distract</label>
                            <Field
                              type="text"
                              class="form-control"
                              placeholder="Distract"
                              name="customer_billing_distract"
                              :class="{ 'is-invalid': errors.customer_billing_distract }"
                            />
                            <div class="invalid-feedback">{{ errors.customer_billing_distract }}</div>
                          </div>
                          <div class="form-group">
                            <label>Zip</label>
                            <Field
                              type="text"
                              class="form-control"
                              name="customer_billing_zip"
                              placeholder="Zip"
                              :class="{ 'is-invalid': errors.customer_billing_zip }"
                            />
                            <div class="invalid-feedback">{{ errors.customer_billing_zip }}</div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="col-md-6">
                      <div class="billing-btn">
                        <h5 class="form-title mb-0">Shipping Address</h5>
                        <a href="javascript:;" class="btn btn-primary"
                          >Copy from Billing</a
                        >
                      </div>
                      <div class="form-group">
                        <label>Name</label>
                        <Field
                          type="text"
                          class="form-control"
                          name="customer_shipping_name"
                          placeholder="Enter Name"
                        />
                      </div>
                      <div class="form-group">
                        <label>Address</label>
                        <Field
                          type="text"
                          class="form-control"
                          name="customer_shipping_address"
                          placeholder="Enter Address 1"
                        />
                      </div>
                      <div class="row">
                        <div class="col-lg-6 col-md-12">
                          <div class="form-group">
                            <label>Country</label>
                            <Field
                              type="text"
                              class="form-control"
                              name="customer_shipping_country"
                              placeholder="Country"
                            />
                          </div>
                          <div class="form-group">
                            <label>City</label>
                            <Field
                              type="text"
                              name="customer_shipping_city"
                              class="form-control"
                              placeholder="City"
                            />
                          </div>
                          <div class="form-group">
                            <label>Phone</label>
                            <vue-tel-input
                              mode="international"
                              class="form-control d-flex"
                              id="phone_shipping"
                              :inputOptions="{ name: 'customer_shipping_phone' }"
                            ></vue-tel-input>
                          </div>
                        </div>
                        <div class="col-lg-6 col-md-12">
                          <div class="form-group">
                            <label>State</label>
                            <Field
                              type="text"
                              class="form-control"
                              placeholder="State"
                              name="customer_shipping_state"
                            />
                          </div>
                          <div class="form-group">
                            <label>Distract</label>
                            <Field
                              type="text"
                              class="form-control"
                              placeholder="Distract"
                              name="customer_shipping_distract"
                            />
                          </div>
                          <div class="form-group">
                            <label>Zip</label>
                            <Field
                              type="text"
                              class="form-control"
                              name="customer_shipping_zip"
                              placeholder="Zip"
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="add-customer-btns text-end">
                  <router-link to="/customers" type="button" class="btn customer-btn-cancel"
                    >Back</router-link
                  >
                  <loading-button name="Save Changes" action="AddCustomer"/>
                </div>
              </div>
            </Form>
          </div>
        </div>
      </div>
    </div>
    <!-- /Page Wrapper -->
  </div>
</template>

<script>
import * as Yup from "yup";
import { Form, Field } from "vee-validate";
import { mapActions, mapState } from "vuex";
export default {
  methods: {
    // ...mapActions(['AddCustomer']),
    onSubmit(Customer){
      this.$store.dispatch("AddCustomer", Customer)
    },
    ...mapActions(['GetCustomer']),
    async fetchCustomers() {
      await this.GetCustomer();
    },
    isLoading(actionName) {
        return this.$store.state.Loading[actionName] || false;
    },
  },
  data() {
    return {
      title: "Add Customers",
      Currency: ["Select Currency", "₹", "$", "£", "€"],
    };
  },
  setup() {
    const schema = Yup.object().shape({
      email: Yup.string().required("Email is required").email("Email is invalid"),
      customer_name: Yup.string().required("Name is required"),
      customer_vat_no: Yup.string().required("VAT is required"),
      customer_cr_no: Yup.string().required("CR is required"),
      customer_contact: Yup.string().required("Phone is required"),
      customer_billing_name: Yup.string().required("Billing Name is required"),
      customer_billing_distract: Yup.string().required("Billing distract is required"),
      customer_billing_address: Yup.string().required("Billing Address is required"),
      customer_billing_country: Yup.string().required("Billing Country is required"),
      customer_billing_city: Yup.string().required("Billing City is required"),
      customer_billing_phone: Yup.string().required("Billing Phone is required"),
      customer_billing_state: Yup.string().required("Billing State is required"),
      customer_billing_zip: Yup.string().required("Billing Zip is required"),
      customer_password: Yup.string().min(6, "Password must be at least 6 characters").required("Password is required"),
    });
    return {
      schema,
    };
  },
  components: {
    Form,
    Field,
  },
  computed: {
    message() {
      return this.$store.state.message;
    },
    color() {
      return this.$store.state.color;
    },
    ...mapState(['customers']),
  },
  mounted() {
    this.fetchCustomers();
  },
};
</script>
