<template>
    <!-- Page Header -->
    <div class="page-header">
      <div class="content-page-header">
        <h5>{{specific != null ? specific : name}}</h5>
        <div class="list-btn">
          <ul class="filter-list">
            <li>
              <a v-if="type == 'modal'" class="btn btn-primary"
                href="javascript:void(0);"
                data-bs-toggle="modal"
                :data-bs-target="'#'+modal"
                ><i class="fa fa-plus-circle me-2" aria-hidden="true"></i>Add
                {{specific != null ? specific : name}}</a
              >
              <router-link v-if="type == 'page'" class="btn btn-primary" :to="'/add-'+name"
                ><i class="fa fa-plus-circle me-2" aria-hidden="true"></i>Add
                {{specific != null ? specific : name}}</router-link
              >
            </li>
          </ul>
        </div>
      </div>
    </div>
    <!-- /Page Header -->
  </template>
  
  <script>
  import { ref } from "vue";
  const currentDate = ref(new Date());
  export default {
    props:['name', 'type','modal','specific'],
    data() {
      return {
        startdate: currentDate,
      };
    },
  };
  </script>
  