<template>
  <div class="main-wrapper">
    <layout-header></layout-header>
    <layout-sidebar></layout-sidebar>

    <!-- Page Wrapper -->
    <div class="page-wrapper">
      <div class="content container-fluid">
        <page-header name="Categories" type="modal" modal="add_category"/>
        <card-products></card-products>
        <!-- Table -->
        <div class="row">
          <div class="col-sm-12">
            <div class="card-table">
              <div class="card-body">
                <div class="table-responsive">
                  <a-table
                    class="stripped table-hover"
                    :columns="columns"
                    :data-source="categories"
                    @change="onChange"
                  >
                    <template #bodyCell="{ column, record }">
                      <template v-if="column.key === 'CategoryImage'">
                        <h2 class="table-avatar">
                          <img
                            class="avatar-img rounded"
                            width="30"
                            height="30"
                            :src="
                              require(`@/assets/img/category/${record.CategoryImage}`)
                            "
                            alt="User Image"
                          />
                        </h2>
                      </template>
                      <template v-else-if="column.key === 'Action'">
                        <div class="d-flex align-items-center">
                          <a
                            class="btn-action-icon me-2"
                            href="javascript:void(0);"
                            data-bs-toggle="modal"
                            data-bs-target="#edit_category"
                            ><i class="feather feather-edit"></i
                          ></a>
                          <a
                            class="btn-action-icon"
                            href="javascript:void(0);"
                            data-bs-toggle="modal"
                            data-bs-target="#delete_modal"
                            ><i class="feather feather-trash-2"></i
                          ></a>
                        </div>
                      </template>
                    </template>
                  </a-table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- /Table -->
      </div>
    </div>
    <!-- /Page Wrapper -->
    <transition name="fade" mode="out-in">
        <loading name="GetCategories"></loading>
    </transition>
    <!-- Add Category Modal -->
    <div class="modal custom-modal fade" id="add_category" role="dialog">
      <div class="modal-dialog modal-dialog-centered modal-md">
        <div class="modal-content">
          <div class="modal-header border-0 pb-0">
            <div class="form-header modal-header-title text-start mb-0">
              <h4 class="mb-0">Add Category</h4>
            </div>
            <button type="button" class="close" data-bs-dismiss="modal" aria-label="Close">
              <span class="align-center" aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12">
                <div class="card-body">
                  <Form 
                    class="form-group-item border-0 pb-0 mb-0"
                    @submit="onSubmitCategory"
                    :validation-schema="CategorySchema"
                    v-slot="{ errors }"
                  >
                    <div class="row">
                      <div class="col-lg-12 col-sm-12">
                        <div class="form-group">
                          <label>Category Name <span class="text-danger">*</span></label>
                          <Field
                            type="text"
                            class="form-control"
                            name="category_name"
                            placeholder="Enter Title"
                            :class="{ 'is-invalid': errors.category_name }"
                          />
                          <div class="invalid-feedback">{{ errors.category_name }}</div>
                        </div>
                      </div>
                      <div class="col-lg-12 col-sm-12">
                        <div class="form-group">
                          <label>Category Type</label>
                          <vue-select 
                            :options="Parent_Category" 
                            placeholder="None"
                            v-model="category_type"
                            name="category_type"
                          />
                        </div>
                      </div>
                      <div class="col-lg-12 col-sm-12">
                        <div class="form-group">
                          <label>Category Color</label>
                          <Field
                            type="color"
                            class="form-control"
                            name="category_color"
                            placeholder="FFFFFF"
                            :class="{ 'is-invalid': errors.category_color }"
                          />
                          <div class="invalid-feedback">{{ errors.category_color }}</div>
                        </div>
                      </div>
                    </div>
                    <div class="modal-footer">
                      <a
                        href="javascript:;"
                        data-bs-dismiss="modal"
                        class="btn btn-primary paid-cancel-btn me-2 dismiss-modal"
                        >Cancel</a
                      >
                      <loading-button name="Add Category" action="AddCategory"/>
                    </div>
                  </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- /Add Category Modal -->
  </div>
</template>

<script>
const columns = [
  {
    title: "#",
    dataIndex: "id",
    sorter: (a, b) => a.NUM.localeCompare(b.NUM),
  },
  {
    title: "Category Name",
    dataIndex: "name",
    sorter: {
      compare: (a, b) => {
        a = a.name.toLowerCase();
        b = b.CategoryName.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "CategoryType",
    dataIndex: "type",
    sorter: {
      compare: (a, b) => {
        a = a.type.toLowerCase();
        b = b.type.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Action",
    key: "Action",
    sorter: true,
  },
];
import { mapActions, mapState } from "vuex";
import { ref } from "vue";
import { Form, Field } from "vee-validate";
import * as Yup from "yup";
import VueCookies from 'vue-cookies'

const currentDate = ref(new Date());
export default {
  data() {
    return {
      title1: "Delete Category",
      startdate: currentDate,
      Parent_Category: ["None", "Coupons", "News", "Plugins", "Themes", "Tutorial"],
      Parent_Category1: ["None", "Coupons", "News", "Plugins", "Themes", "Tutorial"],
      columns,
    };
  },
  setup() {
    const CategorySchema = Yup.object().shape({
      category_name: Yup.string().required("Category Name is required"),
      category_color: Yup.string().required("Category Color is required"),
    });
    return {
      CategorySchema,
    };
  },
  components: {
    Form,
    Field,
  },
  computed: {
    ...mapState(['categories']),
  },
  methods: {
    ...mapActions(['GetCategories']),
    async fetchData() {
      await this.GetCategories();
    },
    onSubmitCategory(category){
      category.category_type = this.category_type
      category.user_id = VueCookies.get("UserIDToken")
      this.$store.dispatch("AddCategory", category)
    },
  },
  mounted() {
    this.fetchData();
  },
};
</script>
