<template>
    <div class="main-wrapper">
      <layout-header></layout-header>
      <layout-sidebar></layout-sidebar>
  
      <!-- Page Wrapper -->
      <div class="page-wrapper">
        <div class="content container-fluid">
            <page-header name="Products" type="modal" modal="add_product"/>
            <card-products></card-products>
  
          <!-- Table -->
          <div class="row">
            <div class="col-sm-12">
              <div class="card-table">
                <div class="card-body">
                  <div class="table-responsive">
                    <a-table
                      class="stripped table-hover"
                      :columns="columns"
                      :data-source="products"
                      @change="onChange"
                    >
                      <template #bodyCell="{ column, record }">
                        <template v-if="column.key === 'PurchasePrice'">
                          <div>{{ record.PurchasePrice }}</div>
                        </template>
                        <template v-else-if="column.key === 'Action'">
                            <div class="d-flex align-items-center">
                            <router-link class="dropdown-item btn-action-icon me-2" :to="'/customer-details/'+record.id"
                                    ><i class="far fa-eye me-2"></i></router-link
                              >
                              <a
                                class="dropdown-item btn-action-icon"
                                href="javascript:void(0);"
                                @click="$store.dispatch('DeleteAPI', {id: record.id, name: 'customer', action:'Customers'})"
                                ><i class="far fa-trash-alt me-2 text-danger"></i></a
                              >
                          </div>
                        </template>
                      </template>
                    </a-table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- /Table -->
        </div>
      </div>
      <transition name="fade" mode="out-in">
      <loading name="GetProducts"></loading>
    </transition>
    </div>
      <!-- Add Product Modal -->
  <div class="modal custom-modal fade" id="add_product" role="dialog">
    <div class="modal-dialog modal-dialog-centered modal-lg">
      <div class="modal-content">
        <div class="modal-header border-0 pb-0">
          <div class="form-header modal-header-title text-start mb-0">
            <h4 class="mb-0">Add Product</h4>
          </div>
          <button type="button" class="close" data-bs-dismiss="modal" aria-label="Close">
            <span class="align-center" aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-12">
              <div class="card-body">
                <Form 
                  class="form-group-item border-0 pb-0 mb-0"
                  @submit="onSubmitProduct"
                  :validation-schema="ProductSchema"
                  v-slot="{ errors }"
                >
                  <div class="row">
                    <div class="col-lg-6 col-sm-12">
                      <div class="form-group">
                        <label>Name <span class="text-danger">*</span></label>
                        <Field
                          type="text"
                          class="form-control"
                          name="product_name"
                          :class="{ 'is-invalid': errors.product_name }"
                        />
                        <div class="invalid-feedback">{{ errors.product_name }}</div>
                      </div>
                    </div>
                    <div class="col-lg-6 col-sm-12">
                      <div class="form-group">
                        <label>SKU</label>
                        <Field
                          type="text"
                          class="form-control"
                          name="product_sku"
                          :class="{ 'is-invalid': errors.product_sku }"
                        />
                        <div class="invalid-feedback">{{ errors.product_sku }}</div>
                      </div>
                    </div>
                    <div class="col-lg-12 col-sm-12">
                      <div class="form-group">
                        <label>Description</label>
                        <Field
                          as="textarea"
                          type="text"
                          class="form-control"
                          name="product_description"
                          :class="{ 'is-invalid': errors.product_description }"
                        />
                        <div class="invalid-feedback">{{ errors.product_description }}</div>
                      </div>
                    </div>
                    <div class="col-lg-6 col-sm-12">
                      <div class="form-group">
                        <label>Sale Price <span class="text-danger">*</span></label>
                        <Field
                          type="text"
                          class="form-control"
                          name="product_sale_price"
                          :class="{ 'is-invalid': errors.product_sale_price }"
                        />
                        <div class="invalid-feedback">{{ errors.product_sale_price }}</div>
                      </div>
                    </div>
                    <div class="col-lg-6 col-sm-12">
                      <div class="form-group">
                        <label>Purchase Price <span class="text-danger">*</span></label>
                        <Field
                          type="text"
                          class="form-control"
                          name="product_purchase_price"
                          :class="{ 'is-invalid': errors.product_purchase_price }"
                        />
                        <div class="invalid-feedback">{{ errors.product_purchase_price }}</div>
                      </div>
                    </div>
                    <div class="col-lg-6 col-sm-12">
                      <div class="form-group">
                        <label>Category <span class="text-danger">*</span></label>
                        <Field 
                            as="select"
                            name="category_id" 
                            class="form-control"
                            id="category_type"
                            :class="{ 'is-invalid': errors.category_id }"
                        >
                        <!-- <option value="" disabled selected>Select Category</option> -->
                        <option :value="category.id" v-for="category in categories" :key="category.id">{{ category.name }}</option>
                        </Field>
                        <div class="invalid-feedback">{{ errors.category_id }}</div>
                      </div>
                    </div>
                    <div class="col-lg-6 col-sm-12">
                      <div class="form-group">
                        <label>Unit <span class="text-danger">*</span></label>
                        <Field 
                            as="select"
                            name="unit_id" 
                            class="form-control"
                            id="category_type"
                            :class="{ 'is-invalid': errors.unit_id }"
                        >
                        <!-- <option value="" disabled selected>Select Category</option> -->
                        <option :value="unit.id" v-for="unit in units" :key="unit.id">{{ unit.name }}</option>
                        </Field>
                        <div class="invalid-feedback">{{ errors.unit_id }}</div>
                      </div>
                    </div>
                    <div class="col-lg-6 col-sm-12">
                      <div class="form-group">
                        <label>Brand <span class="text-danger">*</span></label>
                        <Field 
                            as="select"
                            name="brand_id" 
                            class="form-control"
                            id="category_type"
                            :class="{ 'is-invalid': errors.brand_id }"
                        >
                        <!-- <option value="" disabled selected>Select Category</option> -->
                        <option :value="brand.id" v-for="brand in brands" :key="brand.id">{{ brand.name }}</option>
                        </Field>
                        <div class="invalid-feedback">{{ errors.brand_id }}</div>
                      </div>
                    </div>
                    <div class="col-lg-6 col-sm-12">
                      <div class="form-group">
                        <label>Type <span class="text-danger">*</span></label>
                        <Field 
                            as="select"
                            name="type" 
                            class="form-control"
                            id="type"
                            :class="{ 'is-invalid': errors.type }"
                        >
                        <!-- <option value="" disabled selected>Select Category</option> -->
                        <option :value="type" v-for="type in typeProduct" :key="type.id">{{ type }}</option>
                        </Field>
                        <div class="invalid-feedback">{{ errors.type }}</div>
                      </div>
                    </div>
                  </div>
                  <div class="modal-footer">
                    <a
                      href="javascript:;"
                      data-bs-dismiss="modal"
                      class="btn btn-primary paid-cancel-btn me-2 dismiss-modal"
                      >Cancel</a
                    >
                    <loading-button name="Save Changes" action="AddProduct"/>
                  </div>
                </Form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- /Add Category Modal -->
  </template>
  
  <script>
  const columns = [
    {
      title: "SN",
      dataIndex: "id",
      sorter: (a, b) => a.id.localeCompare(b.id),
    },
    {
      title: "Name",
      key: "name",
      dataIndex: "name",
      sorter: (a, b) => a.name.localeCompare(b.name),
    },
    {
      title: "Sale Price",
      dataIndex: "sale_price",
      sorter: {
        compare: (a, b) => {
          a = a.sale_price.toLowerCase();
          b = b.sale_price.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Purchase Price",
      dataIndex: "purchase_price",
      sorter: {
        compare: (a, b) => {
          a = a.purchase_price.toLowerCase();
          b = b.purchase_price.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Action",
      key: "Action",
      sorter: true,
    },
  ];
  import { Form, Field } from "vee-validate";
  import * as Yup from "yup";
  import { mapActions, mapState } from "vuex";
  import { ref } from "vue";
  const currentDate = ref(new Date());
  export default {
    setup() {
        const ProductSchema = Yup.object().shape({
            product_name: Yup.string().required("Name is required"),
            product_sku: Yup.string().required("SKU is required"),
            product_description: Yup.string().required("Description is required"),
            product_sale_price: Yup.string().required("Sale price is required"),
            product_purchase_price: Yup.string().required("purchase price is required"),
            unit_id: Yup.string().required("Unit is required"),
            brand_id: Yup.string().required("Brand is required"),
            type: Yup.string().required("Type is required"),
            category_id: Yup.string().required("Category is required"),
        });
        return {
            ProductSchema,
            columns,
        };
    },
    computed: {
      ...mapState(['products','typeProduct','categories','brands','units']),
    },
    components: {
        Form,
        Field,
    },
    methods: {
      ...mapActions(['GetProducts','GetCategories','GetBrands','GetUnits']),
      async GetProductsData(){
        await this.GetProducts()
      },
      async GetCategoriesData(){
        await this.GetCategories()
      },
      async GetBrandsData(){
        await this.GetBrands()
      },
      async GetUnitsData(){
        await this.GetUnits()
      },
      onSubmitProduct(product){
        console.log(product)
        this.$store.dispatch("AddProduct", product)
      },
    },
    mounted() {
      this.GetProductsData();
      this.GetCategoriesData();
      this.GetBrandsData();
      this.GetUnitsData();
    },
  };
  </script>
  