const getters = {
    SubtotalAmountExcludingVAT(state) {
      let total = 0;
      state.records.forEach(record => {
        if (record.sale_price !== null && record.quantity !== null) {
        total += parseFloat(record.sale_price) * parseFloat(record.quantity);
        }
      });
      return total.toFixed(2);
    },
    totalAmountExcludingVAT(state, getters) {
      let total = 0;
      state.records.forEach(record => {
        if (record.sale_price !== null && record.quantity !== null) {
          total = -parseFloat(getters.totalDiscount) - -parseFloat(getters.SubtotalAmountExcludingVAT);
        }
      });
      return total.toFixed(2);
    },
    totalAmount(state, getters) {
      let total = 0;
      state.records.forEach(record => {
        if (record.sale_price !== null && record.quantity !== null) {
        total += parseFloat(record.sale_price);
        }
      });
      return total.toFixed(2);
    },
    totalDiscount(state, getters) {
      let total = 0;
      state.records.forEach(record => {
        if (record.sale_price !== null && record.quantity !== null && record.purchase_price !== null) {
          total += parseFloat(record.purchase_price);
        }
      });
      return total.toFixed(2);
    },
    TotalVat(state, getters) {
      let total = 0;
      var tax = 0;
      // if(state.tax[0].rate){
      //   tax = state.tax[0].rate
      // }
      state.records.forEach(record => {
        if (record.sale_price !== null && record.quantity !== null && record.purchase_price !== null) {
          total = parseFloat(getters.totalAmountExcludingVAT) * 15;
        }
      });
      return total.toFixed(2);
    },
    TotalAmountIncludingVAT(state, getters) {
      let total = 0;
      state.records.forEach(record => {
        if (record.sale_price !== null && record.quantity !== null && record.purchase_price !== null) {
          total = parseFloat(getters.totalAmountExcludingVAT) + parseFloat(getters.TotalVat)
        }
      });
      return total.toFixed(2);
    },
}

export default getters