<template>
  <!-- Main Wrapper -->
  <div class="main-wrapper">
    <layout-header></layout-header>
    <layout-sidebar></layout-sidebar>
    <!-- Page Wrapper -->
    <div class="page-wrapper">
      <div class="content container-fluid">
        <blogheader :title="title" :path="path" :text="text" />
        <page-header name="Quotations" type="page"/>
        <!-- Search Filter -->
        <div id="filter_inputs" class="card filter-card">
          <div class="card-body pb-0">
            <div class="row">
              <div class="col-sm-6 col-md-3">
                <div class="form-group">
                  <label>Name</label>
                  <input type="text" class="form-control" />
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group">
                  <label>Email</label>
                  <input type="text" class="form-control" />
                </div>
              </div>
              <div class="col-sm-6 col-md-3">
                <div class="form-group">
                  <label>Phone</label>
                  <input type="text" class="form-control" />
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- /Search Filter -->

        <!-- Table -->
        <div class="row">
          <div class="col-sm-12">
            <div class="card-table">
              <div class="card-body">
                <div class="table-responsive">
                  <a-table
                    class="stripped table-hover"
                    :columns="columns"
                    :data-source="quotations"
                    @change="onChange"
                  >
                    <template #bodyCell="{ column, record }">
                      <template v-if="column.key === 'Status'">
                        <span class="rounded-pill badge bg-primary" v-if="record.status == 0">Draft</span>
                        <span class="rounded-pill badge bg-warning" v-if="record.status == 1">Sent</span>
                        <span class="rounded-pill badge bg-danger" v-if="record.status == 2">Cancel</span>
                        <span class="rounded-pill badge bg-info" v-if="record.status == 3">partially paid</span>
                        <span class="rounded-pill badge bg-success" v-if="record.status == 4">Paid</span>
                      </template>
                      <template v-else-if="column.key === 'proposal_id'">
                        <b>#IQUOO00{{ record.proposal_id }}</b>
                      </template>
                      <template v-else-if="column.key === 'Action'">
                        <div class="d-flex align-items-center">
                              <router-link class="dropdown-item btn-action-icon me-2" :to="'/invoice-details/'+record.id"
                                    ><i class="far fa-eye me-2"></i></router-link
                              >
                              <a
                                class="dropdown-item btn-action-icon"
                                href="javascript:void(0);"
                                @click="$store.dispatch('DeleteAPI', {id: record.id, name: 'invoices', action:'Invoices'})"
                                ><i class="far fa-trash-alt me-2 text-danger"></i></a
                              >
                          </div>
                      </template>
                    </template>
                  </a-table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- /Table -->
      </div>
    </div>
    <!-- /Page Wrapper -->
    <locationmodal :title1="title1" />
  </div>
  <transition name="fade" mode="out-in">
      <loading name="GetQuotations"></loading>
    </transition>
  <!-- /Main Wrapper -->
</template>
<script>
const columns = [
  {
    title: "Quotation ID",
    dataIndex: "proposal_id",
    key: 'proposal_id',
    sorter: (a, b) => a.proposal_id.localeCompare(b.proposal_id),
  },
  {
    title: "Customer",
    dataIndex: "Customer",
    key: "id",
    sorter: (a, b) => a.Customer.localeCompare(b.Customer),
  },
  {
    title: "Created On",
    dataIndex: "issue_date",
    sorter: {
      compare: (a, b) => {
        a = a.issue_date.toLowerCase();
        b = b.issue_date.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Status",
    dataIndex: "Status",
    key: "Status",
    sorter: {
      compare: (a, b) => {
        a = a.Status.toLowerCase();
        b = b.Status.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },

  {
    title: "Action",
    key: "Action",
    sorter: true,
  },
];

import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      title: "Quotations",
      path: "add-quotations",
      text: "Create Quotation",
      title1: "Delete Quotations",
      columns,
    };
  },
  computed: {
    ...mapState(['quotations']),
  },
  methods: {
    ...mapActions(['GetQuotations']),
    async fetchQuotations() {
      await this.GetQuotations();
    },
  },
  mounted() {
    this.fetchQuotations();
  },
};
</script>
