const mutations = {
    REGISTER_DATA(state, User){
      state.RegisterData = User
    },
    LOGIN_DATA(state, User){
      state.LoginData = User
    },
    ADD_CUSTOMER(state, Customer){
      state.added_customer = Customer
    },
    GET_CUSTOMER(state, customer){
      state.customer = customer
    },
    GET_SUPPLIER(state, supplier){
      state.supplier = supplier
    },
    GET_BRANDS(state, brands){
      state.brands = brands
    },
    SET_AUTHENTICATED(state, isAuthenticated) {
      state.isAuthenticated = isAuthenticated;
    },
    ADD_Invoince(state, Invoice){
      state.added_invoice = Invoice
    },
    ADD_Quotation(state, Quotation){
      state.added_quotation = Quotation
    },
    ADD_TAX(state, tax){
      state.added_tax = tax
    },
    ADD_CATEGORY(state, Category){
      state.category = Category
    },
    ADD_STOCK(state, Stock){
      state.stock = Stock
    },
    GET_SUPPLIERS(state, suppliers){
      state.suppliers = suppliers
    },
    CHECK_TOKEN(state, token){
      state.check_token = token
    },
    SET_MESSAGE_POST(state,message){
      state.message = message
    },
    SET_MESSAGE_COLOR(state,message){
      state.color = message
    },
    GET_CUSTOMERS(state, customers){
      state.customers = customers
    },
    GET_QUOTATIONS(state, quotations){
      state.quotations = quotations
    },
    GET_STOCKS(state, stocks){
      state.stocks = stocks
    },
    GET_CATEGORIES(state, categories){
      state.categories = categories
    },
    GET_INVOICES(state, invoices){
      state.invoices = invoices
    },
    GET_PRODUCTS(state, products){
      state.products = products
    },
    GET_UNITS(state, units){
      state.units = units
    },
    GET_DELIVERY_NOTES(state, delivery_notes){
      state.delivery_notes = delivery_notes
    },
    GET_TAX(state, tax){
      state.tax = tax
    },
    REDUCE_PRODUCT_SERVICE_ITEM(state, id) {
      const index = state.records.findIndex(record => record.id === id);
      if (index !== -1) {
        const [removedItem] = state.records.splice(index, 1);
        console.log(removedItem);
        state.products.push(removedItem);
      } else {
        console.error('Item not found');
      }
    },
    SET_TOKEN(state, token) {
      state.token = token;
      VueCookies.set('token', token, '1d'); // Set token in the cookie with 1 day expiration
    },
    SET_USER_ID(state, user_id) {
      state.user_id = user_id;
      VueCookies.set('user_id', user_id, '1d');
    },
    LOADING_API(state, {name, status}){
      state.Loading[name] = status;
    },
}

export default mutations