<template>
  <!-- Flag -->
  <li class="nav-item dropdown has-arrow flag-nav">
    <a
      class="nav-link dropdown-toggle"
      data-bs-toggle="dropdown"
      href="javascript:void(0)"
      role="button"
    >
      
      <img :src="aFlag" alt="" height="20" class="rounded-circle" width="18"/>
      <span class="adminmodule">{{ alang }}</span>
    </a>
    <div class="dropdown-menu dropdown-menu-right">
      <a
        href="javascript:void(0);"
        v-for="(entry, i) in languages"
        :key="`Lang${i}`"
        :value="entry"
        @click="changeLanguage(entry.language)"
        :class="{ active: lan === entry.language }"
        class="dropdown-item"
        data-lang="en"
        :title="$t(entry.title)"
      >
        <flag :iso="entry.iso" data-bs-toggle="tooltip" class="me-2" />
        <span class="align-middle">{{ entry.title }}</span>
      </a>
    </div>
  </li>
  <!-- /Flag -->
</template>
<script>
import { createI18n } from "vue-i18n";
import englishFlag from "@/assets/img/flags/us1.png";
import arabicFlag from "@/assets/img/flags/sa.png";
export default {
  data() {
    return {
      languages: [
        {
          iso: "lr",
          language: "en",
          title: "English",
        },
        {
          iso: "sa",
          language: "ar",
          title: "العربية",
        },
      ],
      alang: "English",
      aFlag: englishFlag,
    };
  },
  methods:{
    changeLanguage(lang){
      this.$i18n.locale = lang;
      localStorage.setItem("lang", lang)
      if(lang=="ar"){
        this.alang = "العربية"
        this.aFlag = arabicFlag
      }
      if(lang=="en"){
        this.alang = "English"
        this.aFlag = englishFlag
      }
    }
  },
  created(){
    console.log(localStorage.getItem("lang"))
    if(localStorage.getItem("lang")=="ar"){
        this.alang = "العربية"
        this.aFlag = arabicFlag
      }
      if(localStorage.getItem("lang")=="en"){
        this.alang = "English"
        this.aFlag = englishFlag
      }
  }
};
</script>
