<template>
    <div class="main-wrapper">
      <layout-header></layout-header>
      <layout-sidebar></layout-sidebar>
  
      <!-- Page Wrapper -->
      <div class="page-wrapper">
        <div class="content container-fluid">
         <page-header name="Brands" type="modal" modal="add_brand"/>
  
          <!-- Search Filter -->
          <div id="filter_inputs" class="card filter-card">
            <div class="card-body pb-0">
              <div class="row">
                <div class="col-sm-6 col-md-3">
                  <div class="form-group">
                    <label>Name</label>
                    <input type="text" class="form-control" />
                  </div>
                </div>
                <div class="col-sm-6 col-md-3">
                  <div class="form-group">
                    <label>Email</label>
                    <input type="text" class="form-control" />
                  </div>
                </div>
                <div class="col-sm-6 col-md-3">
                  <div class="form-group">
                    <label>Phone</label>
                    <input type="text" class="form-control" />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- /Search Filter -->
  
          <card-products></card-products>
  
          <!-- Table -->
          <div class="row">
            <div class="col-sm-12">
              <div class="card-table">
                <div class="card-body">
                  <div class="table-responsive">
                    <a-table
                      class="stripped table-hover"
                      :columns="columns"
                      :data-source="brands"
                      @change="onChange"
                    >
                      <template #bodyCell="{ column, record }">
                        <template v-if="column.key === 'CategoryImage'">
                          <h2 class="table-avatar">
                            <img
                              class="avatar-img rounded"
                              width="30"
                              height="30"
                              :src="
                                require(`@/assets/img/category/${record.CategoryImage}`)
                              "
                              alt="User Image"
                            />
                          </h2>
                        </template>
                        <template v-else-if="column.key === 'Action'">
                          <div class="d-flex align-items-center">
                            <a
                              class="btn-action-icon me-2"
                              href="javascript:void(0);"
                              data-bs-toggle="modal"
                              data-bs-target="#edit_category"
                              ><i class="feather feather-edit"></i
                            ></a>
                            <a
                              class="btn-action-icon"
                              href="javascript:void(0);"
                              data-bs-toggle="modal"
                              data-bs-target="#delete_modal"
                              ><i class="feather feather-trash-2"></i
                            ></a>
                          </div>
                        </template>
                      </template>
                    </a-table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- /Table -->
        </div>
      </div>
      <!-- /Page Wrapper -->
    </div>
  <!-- Add Product Modal -->
  <div class="modal custom-modal fade" id="add_brand" role="dialog">
    <div class="modal-dialog modal-dialog-centered modal-lg">
      <div class="modal-content">
        <div class="modal-header border-0 pb-0 justify-content-center">
          <div class="form-header modal-header-title text-start mb-0">
            <h4 class="mb-0">Add Brand</h4>
          </div>
          <button type="button" class="close" data-bs-dismiss="modal" aria-label="Close">
            <span class="align-center" aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-md-12">
              <div class="card-body">
                <Form 
                  class="form-group-item border-0 pb-0 mb-0"
                  @submit="onSubmitBrand"
                  :validation-schema="BrandSchema"
                  v-slot="{ errors }"
                >
                  <div class="row">
                    <div class="col-lg-6 col-sm-12">
                      <div class="form-group">
                        <label>Name <span class="text-danger">*</span></label>
                        <Field
                          type="text"
                          class="form-control"
                          name="name"
                          :class="{ 'is-invalid': errors.name }"
                        />
                        <div class="invalid-feedback">{{ errors.name }}</div>
                      </div>
                    </div>
                    <div class="col-lg-6 col-sm-12">
                      <div class="form-group">
                        <label>Contact</label>
                        <Field
                          type="text"
                          class="form-control"
                          name="contact"
                          :class="{ 'is-invalid': errors.contact }"
                        />
                        <div class="invalid-feedback">{{ errors.contact }}</div>
                      </div>
                    </div>
                    <div class="col-lg-12 col-sm-12">
                      <div class="form-group">
                        <label>Website</label>
                        <Field
                          type="url"
                          class="form-control"
                          name="website"
                          :class="{ 'is-invalid': errors.website }"
                        />
                        <div class="invalid-feedback">{{ errors.website }}</div>
                      </div>
                    </div>
                    <div class="col-lg-12 col-sm-12">
                      <div class="form-group">
                        <label>Upload Photo</label>
                        <a-upload-dragger
                          name="file"
                          @change="handleChange"
                        >
                          <p class="ant-upload-text">Click or drag file to this area to upload</p>
                          <p class="ant-upload-hint">
                            Support for a single or bulk upload. Strictly prohibit from uploading company data or other band files
                          </p>
                        </a-upload-dragger>
                        <div v-if="imageName" class="d-flex p-3 border rounded col-4 mt-3">
                          <img :src="imageSrc" alt="Preview" width="50" height="50"/>
                          <p class="ms-3">{{ imageName }}</p>
                        </div>
                        <div class="invalid-feedback">{{ errors.website }}</div>
                      </div>
                    </div>
                  </div>
                  <div class="modal-footer">
                    <a
                      href="javascript:;"
                      data-bs-dismiss="modal"
                      class="btn btn-primary paid-cancel-btn me-2 dismiss-modal"
                      >Cancel</a
                    >
                    <loading-button name="Add Brand" action="AddBrand"/>
                  </div>
                </Form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- /Add Category Modal -->
  <transition name="fade" mode="out-in">
      <loading name="GetBrands"></loading>
  </transition>
  </template>
  
  <script>
  const columns = [
    {
      title: "#",
      dataIndex: "id",
      sorter: (a, b) => a.NUM.localeCompare(b.NUM),
    },
    {
      title: "Category Name",
      dataIndex: "name",
      sorter: {
        compare: (a, b) => {
          a = a.name.toLowerCase();
          b = b.name.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Image",
      dataIndex: "image",
      sorter: {
        compare: (a, b) => {
          a = a.image.toLowerCase();
          b = b.image.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Website",
      dataIndex: "website",
      sorter: {
        compare: (a, b) => {
          a = a.website.toLowerCase();
          b = b.website.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Contact",
      dataIndex: "contact",
      sorter: {
        compare: (a, b) => {
          a = a.contact.toLowerCase();
          b = b.contact.toLowerCase();
          return a > b ? -1 : b > a ? 1 : 0;
        },
      },
    },
    {
      title: "Action",
      key: "Action",
      sorter: true,
    },
  ];
  import { mapActions, mapState } from "vuex";
  import { ref } from "vue";
  import { UploadChangeParam } from 'ant-design-vue';
  import { InboxOutlined } from '@ant-design/icons-vue';
  import { message } from 'ant-design-vue';
  import * as Yup from "yup";
  import { Form, Field } from "vee-validate";
  import PictureInput from 'vue-picture-input'
  import { notification } from "ant-design-vue";

  const currentDate = ref(new Date());
  export default {
    data() {
      return {
        title1: "Delete Category",
        startdate: currentDate,
        Parent_Category: ["None", "Coupons", "News", "Plugins", "Themes", "Tutorial"],
        Parent_Category1: ["None", "Coupons", "News", "Plugins", "Themes", "Tutorial"],
        columns,
        imageName: null,
        imageSrc: null,
      };
    },
    setup() {
        const BrandSchema = Yup.object().shape({
          name: Yup.string().required("Name is required"),
          contact: Yup.string().required("Contact required"),
          website: Yup.string().matches(
            /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
            'Enter correct url!'
        )
        .required('Please enter website'),
        });
        return {
            BrandSchema,
        };
    },
    components: {
        Form,
        Field,
        InboxOutlined,
        UploadChangeParam
    },
    computed: {
      ...mapState(['brands']),
    },
    methods: {
      ...mapActions(['GetBrands']),
      async fetchBrands() {
        await this.GetBrands();
      },
      handleChange(info) {
        $('.ant-upload-list').remove()
        const file = info.file.originFileObj;
        if (file) {
          this.imageName = file.name;
          this.imageSrc = URL.createObjectURL(file);
        }
      },
      onSubmitBrand(brand){
        if(this.imageSrc == null){
          notification['info']({
            message: `Error`,
            description: 'Please upload an image',
          });
        }else{
          brand.image = this.imageSrc
          this.$store.dispatch("AddBrand", brand)
        }
        
      },
    },
    mounted() {
      this.fetchBrands();
    },
  };
  </script>
  