          Shipping Address
        </button>
      </h2>
      <div
        id="ShippingAddress"
        class="accordion-collapse collapse"
        aria-labelledby="headingThree"
        data-bs-parent="#accordionExample"
      >
        <div class="accordion-body">
          <div class="text-end">
            <button class="btn btn-primary" type="button">Same as billing</button>
          </div>
          <div v-for="(field, index) in shippingAddressFields" :key="index">
            <div class="col-lg-12 col-sm-12">
              <div class="form-group">
                <label>{{ formatLabel(field.name) }}</label>
                <div class="input-group">
                  <Field
                    :as="field.as ? field.as : 'input'"
                    :name="field.name"
                    :type="field.type"
                    class="form-control mt-2"
                    :class="{ 'is-invalid': errors[field.name] }"
                    :placeholder="field.type === 'password' ? '********' : ''"
                    v-model="shippingAddressFields[index].value"
                  >
                  <template v-if="field.as === 'select'">
                    <option value="">Select an option</option>
                    <option value="option1">Option 1</option>
                    <option value="option2">Option 2</option>
                  </template>
                  </Field>
                  <div class="invalid-feedback">{{ errors[field.name] }}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
            </div>
          </div>
          <div class="modal-footer">
            <a
              href="javascript:;"
              data-bs-dismiss="modal"
              class="btn btn-primary paid-cancel-btn me-2 dismiss-modal"
              >Cancel</a
            >
              <loading-button name="Add Vendor" action="AddVendor"/>
          </div>
        </Form>
      </div>
    </div>
  <!-- /Add Vendor Modal -->

    <!-- Edit Vendor Modal -->
    <div class="modal custom-modal fade" id="edit_vendor" role="dialog">
      <div class="modal-dialog modal-dialog-centered modal-lg">
        <Form 
          class="modal-content"
          @submit="onAddVendor"
          :validation-schema="schema"
          v-slot="{ errors }"
        >
          <div class="modal-header border-0 pb-0">
            <div class="form-header modal-header-title text-start mb-0">
              <h4 class="mb-0">Edit Vendor</h4>
            </div>
            <button type="button" class="close" data-bs-dismiss="modal" aria-label="Close">
              <span class="align-center" aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="accordion" id="accordionExample">
    <!-- Basic Info Section -->
    <div class="accordion-item">
      <h2 class="accordion-header" id="headingOne">
        <button
          class="accordion-button collapsed"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#basicInfo"
          aria-expanded="true"
          aria-controls="basicInfo"
        >
          Basic Info
        </button>
      </h2>
      <div
        id="basicInfo"
        class="accordion-collapse collapse"
        aria-labelledby="headingOne"
        data-bs-parent="#accordionExample"
      >
        <div class="accordion-body">
          <div v-for="(field, index) in basicInfoFields" :key="index">
            <div class="col-lg-12 col-sm-12">
              <div class="form-group">
                <label>{{ formatLabel(field.name) }}</label>
                <div class="input-group">
                  <Field
                    :as="field.as ? field.as : 'input'"
                    :name="field.name"
                    :type="field.type"
                    class="form-control mt-2"
                    :class="{ 'is-invalid': errors[field.name] }"
                    :placeholder="field.type === 'password' ? '********' : ''"
                  >
                    <template v-if="field.as === 'select'">
                      <option value="">Select an option</option>
                      <option value="option1">Option 1</option>
                      <option value="option2">Option 2</option>
                    </template>
                  </Field>
                  <div class="invalid-feedback">{{ errors[field.name] }}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Billing Address Section -->
    <div class="accordion-item">
      <h2 class="accordion-header" id="headingTwo">
        <button
          class="accordion-button collapsed"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#BillingAddress"
          aria-expanded="true"
          aria-controls="BillingAddress"
        >
          Billing Address
        </button>
      </h2>
      <div
        id="BillingAddress"
        class="accordion-collapse collapse"
        aria-labelledby="headingTwo"
        data-bs-parent="#accordionExample"
      >
        <div class="accordion-body">
          <div v-for="(field, index) in billingAddressFields" :key="index">
            <div class="col-lg-12 col-sm-12">
              <div class="form-group">
                <label>{{ formatLabel(field.name) }}</label>
                <div class="input-group">
                  <Field
                    :as="field.as ? field.as : 'input'"
                    :name="field.name"
                    :type="field.type"
                    class="form-control mt-2"
                    :class="{ 'is-invalid': errors[field.name] }"
                    :placeholder="field.type === 'password' ? '********' : ''"
                    v-model="billingAddressFields[index].value"
                  >
                    <template v-if="field.as === 'select'">
                      <option value="">Select an option</option>
                      <option value="option1">Option 1</option>
                      <option value="option2">Option 2</option>
                    </template>
                  </Field>
                  <div class="invalid-feedback">{{ errors[field.name] }}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Shipping Address Section -->
    <div class="accordion-item">
      <h2 class="accordion-header" id="headingThree">
        <button
          class="accordion-button collapsed"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#ShippingAddress"
          aria-expanded="true"
          aria-controls="ShippingAddress"
        >
          Shipping Address
        </button>
      </h2>
      <div
        id="ShippingAddress"
        class="accordion-collapse collapse"
        aria-labelledby="headingThree"
        data-bs-parent="#accordionExample"
      >
        <div class="accordion-body">
          <div class="text-end">
            <button class="btn btn-primary" type="button">Same as billing</button>
          </div>
          <div v-for="(field, index) in shippingAddressFields" :key="index">
            <div class="col-lg-12 col-sm-12">
              <div class="form-group">
                <label>{{ formatLabel(field.name) }}</label>
                <div class="input-group">
                  <Field
                    :as="field.as ? field.as : 'input'"
                    :name="field.name"
                    :type="field.type"
                    class="form-control mt-2"
                    :class="{ 'is-invalid': errors[field.name] }"
                    :placeholder="field.type === 'password' ? '********' : ''"
                    v-model="shippingAddressFields[index].value"
                  >
                  <template v-if="field.as === 'select'">
                    <option value="">Select an option</option>
                    <option value="option1">Option 1</option>
                    <option value="option2">Option 2</option>
                  </template>
                  </Field>
                  <div class="invalid-feedback">{{ errors[field.name] }}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
            </div>
          </div>
          <div class="modal-footer">
            <a
              href="javascript:;"
              data-bs-dismiss="modal"
              class="btn btn-primary paid-cancel-btn me-2 dismiss-modal"
              >Cancel</a
            >
            <button
              type="submit"
              :disabled="isLoading('AddVendor')"
              class="btn btn-primary paid-continue-btn"
              >
              Add Vendor
              <img src="@/assets/img/loader.gif" v-if="isLoading('AddVendor')" width="20" class="ms-3">
              </button>
          </div>
        </Form>
      </div>
    </div>
  <!-- /Add Vendor Modal -->
  </div>
  <transition name="fade" mode="out-in">
      <loading name="GetSuppliers"></loading>
  </transition>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { Form, Field } from "vee-validate";
import * as Yup from "yup";
import { notification } from "ant-design-vue";
const columns = [
  {
    title: "#",
    dataIndex: "id",
    sorter: (a, b) => a.id.localeCompare(b.id),
  },
  {
    title: "Name",
    dataIndex: "name",
    key: "name",
    sorter: {
      compare: (a, b) => {
        a = a.name.toLowerCase();
        b = b.name.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Contact",
    dataIndex: "contact",
    sorter: {
      compare: (a, b) => {
        a = a.contact.toLowerCase();
        b = b.contact.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Email",
    dataIndex: "email",
    sorter: {
      compare: (a, b) => {
        a = a.email.toLowerCase();
        b = b.email.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Balance",
    dataIndex: "balance",
    sorter: {
      compare: (a, b) => {
        a = a.balance.toLowerCase();
        b = b.balance.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Last Login At",
    dataIndex: "last_login_at",
    sorter: {
      compare: (a, b) => {
        a = a.last_login_at.toLowerCase();
        b = b.last_login_at.toLowerCase();
        return a > b ? -1 : b > a ? 1 : 0;
      },
    },
  },
  {
    title: "Action",
    key: "Action",
    sorter: true,
  },
];

export default {
  data() {
    return {
      title1: "Delete Vendor",
      columns,
    };
  },
  components: {
    Form,
    Field,
  },
  setup() {
    const schema = Yup.object().shape({
        // Basic Info Validation
        name: Yup.string().required("Name is required"),
        contact: Yup.string().required("Contact is required"),
        email: Yup.string().required("Email is required").email("Email is invalid"),
        password: Yup.string()
          .min(6, "Password must be at least 6 characters")
          .required("Password is required"),
        vat: Yup.string().required("VAT Number is required")
        .min(15, "Password must be at least 15 characters"),
        cr: Yup.string().required("CR Number is required"),

        // Billing Address Validation
        billing_name: Yup.string().required("Company Legal Name is required"),
        billing_country: Yup.string().required("Country is required"),
        billing_state: Yup.string().required("State is required"),
        billing_city: Yup.string().required("City is required"),
        billing_phone: Yup.string().required("Phone is required"),
        billing_zip: Yup.string().required("Zip Code is required"),
        billing_address: Yup.string().required("Address is required"),

        // Shipping Address Validation
        shipping_name: Yup.string().required("Company Legal Name is required"),
        shipping_country: Yup.string().required("Country is required"),
        shipping_state: Yup.string().required("State is required"),
        shipping_city: Yup.string().required("City is required"),
        shipping_phone: Yup.string().required("Phone is required"),
        shipping_zip: Yup.string().required("Zip Code is required"),
        shipping_address: Yup.string().required("Address is required"),
        });
    return {
      schema,
    };
  },
  computed: {
    ...mapState(['suppliers']),
    basicInfoFields() {
      return [
        { name: "name", type: "text" },
        { name: "contact", type: "number" },
        { name: "email", type: "email" },
        { name: "password", type: "password" },
        { name: "vat", type: "number" },
        { name: "cr", type: "number" }
      ];
    },
    billingAddressFields() {
      return [
        { val: 'name', name: "billing_name", type: "text", value:"" },
        { val: 'country', name: "billing_country", type: "select",as: 'select',value:"" },
        { val: 'state', name: "billing_state", type: "select",as: 'select',value:"" },
        { val: 'city', name: "billing_city", type: "select",as: 'select',value:"" },
        { val: 'phone', name: "billing_phone", type: "number",value:"" },
        { val: 'zip_code', name: "billing_zip", type: "text",value:"" },
        { val: 'address', name: "billing_address", type: "text", as: 'textarea', value:"" }
      ];
    },
    shippingAddressFields() {
      return [
        { val: 'name', name: "shipping_name", type: "text",value:"" },
        { val: 'country', name: "shipping_country", type: "select", as: 'select',value:"" },
        { val: 'state', name: "shipping_state", type: "select", as: 'select',value:"" },
        { val: 'city', name: "shipping_city", type: "select", as: 'select',value:"" },
        { val: 'phone', name: "shipping_phone", type: "number", value:"" },
        { val: 'zip_code', name: "shipping_zip", type: "text", value:"" },
        { val: 'address', name: "shipping_address", type: "textarea", as: 'textarea', value:"" }
      ];
    }
  },
  methods: {
    ...mapActions(['GetSuppliers']),
    async fetchSuppliers() {
      await this.GetSuppliers({notification: notification});
    },
    // copyBillingToShipping() {
    //     this.billingAddressFields.forEach((billingField, index) => {
    //     const shippingField = this.shippingAddressFields[index];
    //     if (billingField.val === shippingField.val) {
    //       billingField.value = shippingField.value;
    //     }
    //   });
    // },
    isLoading(actionName) {
        return this.$store.state.Loading[actionName] || false;
    },
    onAddVendor(Vendor){
      this.$store.dispatch("AddVendor",{Vendor: Vendor, notification: notification})
    },
    formatLabel(name) {
      return name.split("_").map(word => word.charAt(0).toUpperCase() + word.slice(1)).join(" ");
    }
  },
  mounted() {
    this.fetchSuppliers();
  },
};
</script>
