<template lang="">
    <div class="w-100 h-100 text-center loading-icon position-absolute" v-if="isLoading(name)">
        <loading-outlined class="ms-3 fs-1"/>
    </div>
</template>
<script>
import {LoadingOutlined} from '@ant-design/icons-vue';

export default {
    props:['name'],
    components: {
        LoadingOutlined
    },
    methods: {
    isLoading(actionName) {
      return this.$store.state.Loading[actionName] || false;
    },
  },
}
</script>
